main{
    background-color: #181818;
}

.load-video{
    height: 100vh;
    overflow: hidden;
}

@keyframes placeHolderShimmer{
    0%{
        background-position: -468px 0
    }
    100%{
        background-position: 468px 0
    }
}

.linear-background {
    animation-duration: 1s;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-name: placeHolderShimmer;
    animation-timing-function: linear;
    background: linear-gradient(to right, #5f5b5b3b 8%, #44414100 18%, #7d767614 33%);
    background-size: 1000px 104px;
    height: 200px;
    position: relative;
    overflow: hidden;
}

.video{
    margin: 0 10vw;
    display: flex;
    justify-content: center;
    background-color: transparent;
    &__thumbnail{
        width: 35%;
        margin: 7px 9px;
        border-radius: 20px;
        flex: 0 0 35%;
    }
    &__details{
        display: flex;
        flex-direction: column;
        color: #aaaaaa;
        margin: 7px 0;
        flex: 1;
    }
    &__title{
        font-size: 30px;
        color: white;
    }
    &__meta{
        display: flex;
        margin: 10px 0;
    }
    &__seperator{
        padding: 0 5px;
    }
    &__description{
        margin: 10px 0;
        &--load{
            width: 100vw;
            height: 100%;
        }
    }
}

@media screen and (max-width: 900px) {
    .video{
        flex-direction: column;
        &__thumbnail{
            width: 100%;
            margin: 0;
        }
    }
}