.video__player{
    background-color: #181818;
    min-height: 100vh;
    padding: 0 10vw;
    justify-content: center;
}

.auto-resizable-iframe {
    padding: 35px 0;
    max-width: 35em;
  }
  
  .auto-resizable-iframe > div {
    position: relative;
    padding-bottom: 75%;
    height: 0px;
  }
  
  .auto-resizable-iframe iframe {
    position: absolute;
    top: 0px;
    left: 0px;
    max-width: 100%;
    width: 100%;
    height: 100%;
  }